    //         title: 'Notification',
    //         body: 'TradingView Charting Library API works correctly',
    //         callback: () => {
    //           // eslint-disable-next-line no-console
    //           console.log('Noticed!')
    //         },
    //       })
    //     )
    //     button.innerHTML = 'Check API'
    //   })
